/*Helpers*/
.left{
    margin: 30px 0px 30px 0px;
}
.alignMiddle{

    align-self: center;

}

h1,h2,h3,h4,h5,h6{
    margin:0px;
}

/***************/


/*Nav bar*/

.navbar-nav,.navbar-default{
    color:#0066C9;
}
.bg-dark{
    /* background-color: #343a40!important; */
    background-color: white !important;
    color: #343a40!important;

}

.navbar-dark .navbar-nav .nav-link {

    color:#0066C9;

}

.navbar-dark .navbar-nav .nav-link:hover, .navbar-dark .navbar-brand:hover{

    color:#0066C9;
    font-weight: bold;

} 



a.navbar-brand{

    color:#0066C9;
    text-align: left;

}


.navbar-dark .navbar-brand{

    color:#0066C9;

}

.navbar-toggler{
    background-color: #0066C9;
}

/***************/

/* Header*/

.home-header{

    /*background: linear-gradient(6.05deg, #FFFFFF -35.63%, rgba(255, 255, 255, 0) 108.01%), #FBB31D;*/
    background-color: white;
    color:#0066c9;

}

.jumbotron{

        padding:0px;
        margin:0px;

    
}

.header-container{
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-top: 3px solid;
    border-bottom: 3px solid;
    padding: 1rem 0rem 0.2rem 0rem;
     
}

.logo{
    max-width: 80px;
    height: 55px;
    padding: 2px 0px 17px 10px;
    box-sizing: content-box;
    
}

.header-title{

    text-align: left;
    color:#0066c9;   
    font-weight: bold;
    font-size: 2.5rem;

}

/***************/

/* Welcome Msg*/
.text-welcome-container{

    padding: 1.5rem 0rem; 

}
.welcome-text{
    text-align: justify;
    color: #0066c9;
    margin:0px;
}
.template-main-title.{
    text-align: left;
}
.template-title{
    text-align: justify;
    padding:0rem;
    color: #0066c9;
    
}

/***************/



/*boxes selection section*/
.selector-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap:3px 4px;

}

.home-selector{
    min-width: 200px;
    color:white;
    display:grid;
    grid-template-rows: 80% 20%;
    align-items: center;
    justify-items: center;
    height: auto;
    /*background-color: #0066C9;*/
    border: 2px solid white;
    padding-bottom: 1.5rem;
    
}
.home-title{
    display: flex;
    justify-content: center;
    width: 100%;
    padding:25px 10px;
    
}

.home-description-text{
    color:#0066c9;
    font-size: 1rem;
    text-align: justify;
}


.crea{
    background-color: #00D4AE;
}
.experimenta{
    background-color: #86EB83
}

.descubre{
    background-color:#00B6D6;
}

.confia{
    background-color: #0092E3;
}
.selector-icon{
    width: 35px;
}


/***********************/

/*.select-action-title{
    text-align: left;
    margin: 15px 0px 10px 0px;
    padding: 0px;
}*/

/*Carousel*/

.carousel-inner{

    display:grid;
    grid-template-columns: 1fr 1fr 1fr;

}

.card{
    height: 354px;
    width: 100%;
    font-size: .7rem;

}


.carousel-item .carousel-item{

    display: block;
    
}



.slick-prev:before, .slick-next:before{

    color:black;

}

.slick-slide {
    box-sizing: border-box;
    
   
    padding: 0 8px;
    
  
}

.template-slider-container{
    margin-top:2rem;
}



.selector-container{
    display: flex;
    /*justify-content: center;*/
    align-self: center;
}
.template-selection-title{
    color:#0066c9;
    padding: 1rem;
    text-align: left;
    
  
}

.slick-slide .slick-active{

    height: 300px;

}

.crear-container{
    background-color: #0066C9;
    color:white;
    display: flex;
    justify-items: baseline;
    align-items: flex-start;
    padding:10px 10px;
    /*margin-top:2rem;
    text-align: left;*/
}

.divider{
    display: block;
    margin: 0 auto;
    width: 80%;
    padding: 30px 0px;
    box-shadow: 0 1em 1em -1.5em rgba(0, 0, 0, .25);
}


/************************/






/*Media Queries*/

@media (max-width: 700px) {
    .container {
      width: 100%;
      padding:0px;
  }
  .home-text-welcome,.template-title {
      padding:1rem 2rem;
  }

  .slick-slider{
    width: 300px;
    margin: 0 auto;
  }

  .navbar-toggler-icon {
      width: 1rem;
      height: 1rem;
      vertical-align: initial;
  }

  .navbar-brand{
    padding: 1rem;
  }

  .header-title{
    font-size: 1.5rem;
  }

  .logo{
    max-width: 30px;
    height: 30px;
    padding: 2px 0px 5px 5px;
    box-sizing: border-box;
  }

  .header-container{
      padding:0.5rem;
  }

  .home-selector{
      padding:0px;
      border:0px;
      grid-template-rows: 60% 30%;
  }

  .text-welcome-container {
    padding:1rem;
    margin:0px;
}

.template-title{
    padding:1rem;
    font-size: 0.8rem;
}



.row{
    margin:0px;
}
.crear-container{
    margin: 1rem 0px;
}
p.home-description-text{
    padding:1rem;
}
}
@media (max-width:500px){
    .slick-slider {
        width: 250px;
        margin: 0 auto;
    }

}

